<template>
         <td class="tab">
                {{tool.comments}} 
        </td>
        <td class="tab">
                {{tool.delayCause}} 
        </td>
            
        <td class="tab" >
                {{tkulString}} 
        </td>
        <td class="tab">
                {{tool.ip}} 
        </td>
        <td class="tab">
                {{tool.Title}} 
        </td>
        
</template>

<script>
// import axios from "axios"
export default {
 name: "Checks", 
 props:["tool"],
 data(){
    return{
        isShow: true,
        editShow: true,
        cancBtn_show: false,
        del: true,
        saveBtn: false,
        id: null,
        tkulimBoolFields:[],
        tkulString: ""        
    }
 },
  beforeMount(){
     this.tkulString =Object.keys(this.tool.details.boolChecks).filter(boolCheck=>!this.tool.details.boolChecks[boolCheck]).toLocaleString()
   
  },  
 methods:{
         updatetools(){
              this.editShow = false
              this.isShow = false
              this.cancBtn_show = true
              this.del = false
              this.saveBtn = true   
            },
            
            
            cancel(){
              this.editShow = true
              this.isShow = true
              this.cancBtn_show = false
              this.del = true
              this.saveBtn = false
            },           
         }  
      }
</script>

<style scoped>

 td{
      width:var(--table-header-width);
      text-align: center;
        padding: 12px 0px;
      height: 45px;
 }

</style>