        this.$swal.close()
        var query =window.location.href.split('?')[1]
         var reportId = query.split('=')[1]
            try{
              if(!this.$isDjango){
                   const res = await axios.get(this.$url+`/_api/web/Lists/GetByTitle('reports')/Items?$filter=reportId eq '${reportId}'`)
                   this.reports = res.data.value[0];
                                      console.log(this.reports)

                  // this.reports = this.reports.filter(report=>report.reportId ==reportId)[0]; 
