              console.log(this.reports)
              console.log(value)
                this.reports["submitDate"] = moment(new Date(this.reports["submitDate"]),'LLLL','he').format('DD/MM יום dddd')
                if(this.reports.mahlaka =='VC' || this.reports.mahlaka == 'VIP'){
                    this.exportTitle.push(this.reports.submitter +" :מגיש",this.reports.submitDate + " :תאריך", this.reports.mahlaka + " :מחלקה")
                    this.exportTitle.push("מיקום: " + value)
                  }
                else{
                    this.exportTitle.push(this.reports.submitter +" :מגיש",this.reports.submitDate + " :תאריך")
                    this.exportTitle.push("מחלקה: "+ this.reports.mahlaka)
                    this.exportTitle.push("מיקום: " + value)
                    console.log(this.exportTitle)
                }
                
                console.log(this.exportTitle)
                     
            }
          
            catch(e){
              console.error(e);
            }

              console.log(this.reports.tkulim)
            if(typeof this.reports.tkulim =='string'){
                this.reports.tkulim = await this.asyncParse(this.reports.tkulim)
            }
              console.log(this.reports.tkulim)
              var newArr = []
                Object.keys(this.reports.tkulim).forEach((tool)=>{
                newArr.push(this.reports.tkulim[tool])
              })
            // if( typeof this.reports.tkulim[0].details == 'string'){
            //   this.reports.tkulim.forEach((tool)=>{
            //     tool.details = JSON.parse(tool.details)
            //   })
            // }
            this.jsonTkulim = [...newArr]
            console.log(this.jsonTkulim)
            this.getTkulimHeaders();
            
            // var header = this.$refs.fix[0].clientwidth;
        
            console.log(header)
            
    },
      

      methods:{
         
        asyncParse(jsonTkulim){
          return new Promise((resolve)=>{
            resolve(JSON.parse(jsonTkulim))
          })
        },
        getTkulimHeaders(){
           this.jsonTkulim.forEach((tool)=>{
             tool["tkulString"] =Object.keys(tool.details.boolChecks).filter(boolCheck=>!tool.details.boolChecks[boolCheck]).toLocaleString()
           })
           console.log(this.jsonTkulim)
           this.isFinished = true;
        },
           
    }
}
</script>


